






























































































import { Component, Vue } from "vue-property-decorator"

import { $Pages } from "@/myStore"
import { $Validator, $Obstacle } from "@/plugins"

@Component({
    computed: {
        error: {
            get: () => $Pages.$mailer.error,
            set: (v) => $Pages.$mailer.error = v,
        },
        success: {
            get: () => $Pages.$mailer.success,
            set: (v) => $Pages.$mailer.success = v,
        }
    }
})
export default class Contact extends Vue {
    error!: string
    success!: string
    $refs!: {
        reCaptcha
        msg
        send
    }
    name = ''
    email = ''
    subject = ''
    msg = ''

    name_err = ''
    email_err = ''
    subj_err = ''
    msg_err = ''

    showCaptcha = false

    mounted () {
        let _this = this
        // @ts-ignore
        grecaptcha.ready(() => {
            // @ts-ignore
            grecaptcha.render(this.$refs.reCaptcha, {
                'sitekey': '6LdDQ9MZAAAAAAXOm_j-i-gaGUjqzNcIDDDyAXzw',
                'error-callback': function (err) {
                    _this.error = err
                },
                'callback': function (token: string) {
                    $Obstacle.create(_this.$refs.send, {
                        icon: '',
                        action: function () {
                            $Pages.$mailer.send({
                                name: _this.name,
                                email: _this.email,
                                subject: _this.subject,
                                message: _this.msg,
                                token: token
                            }).then(done => {
                                $Obstacle.destroy(_this.$refs.send)
                                // @ts-ignore
                                grecaptcha.reset()
                                _this.showCaptcha = false
                                if (done)
                                {
                                    _this.name = _this.email = _this.subject = _this.msg = ''
                                    _this.$refs.msg.textContent = ''
                                }
                            })
                        }
                    })
                }
            })
        })
    }
    beforeDestroy () {
        this.error = this.success = ''
    }

    setMsg (e) {
        this.msg = e.target.textContent
        if (this.msg_err)
        {
            this.msg_err = ''
        }
    }


    send () {
        if (this.validate())
        {
            this.showCaptcha = true

        }
    }

    validate () {
        const schema = [
            {
                fieldName: 'Email',
                data: this.email,
                rules: {
                    required: true,
                    email: true,
                }
            },
            {
                fieldName: 'Name',
                data: this.name,
                rules: {
                    required: true,
                    string: true,
                    min: 3,
                    max: 50
                }
            },
            {
                fieldName: 'Subject',
                data: this.subject,
                rules: {
                    required: true,
                    string: true,
                    min: 3,
                    max: 100
                }
            },
            {
                fieldName: 'Message',
                data: this.msg,
                rules: {
                    required: true,
                    string: true,
                    min: 20,
                    max: 4000
                }
            }
        ]

        if ($Validator.validate(schema))
        {
            return true
        }
        const errors = $Validator.getErrors()
        this.email_err = errors[ 'Email' ]
        this.name_err = errors[ 'Name' ]
        this.subj_err = errors[ 'Subject' ]
        this.msg_err = errors[ 'Message' ]
    }

}
